import { createApp, h, markRaw } from 'vue'

import MainPage from "@/pages/MainPage";
import NotFoundPage from "@/pages/NotFoundPage";
import BalticEnergyPrivacy from "@/pages/BalticEnergyPrivacy";

const DefaultComponent = markRaw({
    render: () => h('div', 'Loading…')
})

const routes = {
    '/': MainPage,
    '/baltic-energy/privacy': BalticEnergyPrivacy
}

const Router = {
    data: () => ({
        currentRoute: window.location.pathname
    }),

    computed: {
        CurrentComponent() {
            return routes[this.currentRoute] || NotFoundPage
        }
    },

    render() {
        return h(this.CurrentComponent || DefaultComponent)
    }
}

createApp(Router).mount('#app')
